export default {
	data() {
		return {
			commdity: [], //商品
			slideshow: [], //轮播
			show: false,
			popupimg:'',
			step:'1'
		}
	},
	onLoad(news) {
		// console.log(news)
		var item = JSON.parse(decodeURIComponent(news.news));
		console.log(item)
		this.commdity = item;
		this.slideshow = item.slideshow
		console.log(this.slideshow)
		this.popupimg = item.slideshow[0].slideshow
		// console.log(this.popupimg)
	},
	methods: {
		buy() {
			this.show = !this.show
		},
		onClose() {
			this.show = false
		},
		onChange(event) {
		    console.log(event.detail);
			this.step = event.detail;
		},
		next(){
			var commdity = JSON.stringify(this.commdity);
			var steps = this.step;
			uni.navigateTo({
				url:'../ack_order/ack_order?comm=' + encodeURIComponent(commdity) + "&step=" + steps
			})
		}
	}
}
